@import './main.scss';

    .projects {
        box-sizing: border-box;
            padding-top: 2rem;
            background-color: $gray;
            margin: auto;
            @include text_generic;
            display: flex;
            flex-direction: column;
    
            &_title {
                @include title_section;
                margin-bottom: 1rem;
            }
    
            &_list {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: 1fr;
                padding: 0.5rem 1rem 1rem 1rem;    
                &_item {

                     &_link {
                        display: grid;
                        grid-template-columns: 1fr;
                        grid-template-rows: 2fr 1fr;
                        text-decoration: none;
                        gap:1rem;
                        pointer-events: none;
                        cursor: default;

                        &_figure {
                            padding: 0.15rem;
                            width: fit-content;
                            margin: auto;
                            &_img {
                                width: 300px;
                                height: 180px;
                            }
                        }
                    
                        &_container {
                            grid-row: 2/3;
                            padding: 0.5rem;
                    
                            &_title {
                                font-weight: 400;
                                text-transform: capitalize;
                                margin-bottom: 0.5rem;
                                font-size: 0.8rem;
                                text-align: left;
                                color: white;
                            }
                    
                            &_text {
                                font-size: 0.8rem;
                                color: white;
                            }
                        }
                     }
    

                }
    
                &_item:active {
                    transform: scale(1.05);
                    box-shadow: 0px 0px 5px $green;
                    background-color: #191919;
                }
            }
    
        }

@media(min-width: 768px){
    .projects {
            scroll-snap-align: start;
            padding-top: 4rem;
            height: 100vh;
    
            &_list {
                display: grid;
                margin: auto;
                max-width: 90vw;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 175px;
                overflow-y: scroll;
                overflow-x: hidden;
                padding: 0.5rem 1rem 3rem 1rem;
    
                &_item {
                    height: 175px;
                    margin: 0.2rem;
                    text-decoration: none;
                    @include flex_center;

                    &_link{
                        display: grid;
                        grid-template-columns: 1fr 1fr;
                        grid-template-rows: 1fr;
                        color:white;
                        row-gap: 1.5rem;
                        column-gap: 0.5rem;

                        &_figure {
                                grid-column: 1/2;
                                grid-row: 1/2;
                                padding: 0.10rem;
                        
                                &_img {
                                    width: 150px;
                                    height: 100px;
                                }
                            }
                        
                            &_container {
                                grid-column: 2/3;
                                grid-row: 1/2;
                                padding: 0.15rem;
                                margin-bottom: 0.65rem;
                        
                                &_title {
                                    font-size: 0.9rem;
                                    color:white;
                                    font-weight: 400;
                                    padding-bottom: 0.5rem;
                                    text-align: left;
                                }
                        
                                &_text {
                                    font-size: 0.8rem;
                                    color:white;
                                }
                            }
                    }
                    
    
                    
                }
                &_item a{
                    text-decoration: none;
                }
                &_item:hover {
                    transform: scale(1.05);
                    box-shadow: 0px 0px 5px $green;
                    background-color: #191919;
                }
            }
    
        }
}
@media(min-width: 1200px){
    .projects {
            scroll-snap-align: start;
            max-height: 100vh;
            max-width: $width-80;
    
            &_title {
                margin-bottom: 2rem;
            }
    
            &_list {
                display: grid;
                max-height: 80vh;
                grid-template-rows: 250px;
                padding: 1rem 0rem 1.2rem 3rem;
    
                &_item {
                    max-width: 90%;
                    height: 200px;

                    &_link{
                        &_figure {
                                padding: 1rem;

                                &_img {
                                    width: 250px;
                                    height: 145px;
                                }
                            }

                            &_container {
                                padding: 1rem;

                                &_title {
                                    margin-bottom: 1rem;
                                    color: white;
                                    font-size: 1rem;
                                }

                                &_text {
                                    font-size: 1rem;
                                    color: white;
                                }
                            }
                        }                    
                }
    
                &_item:hover {
                    transform: scale(1.1);
                    box-shadow: 0px 0px 10px $green;
                    background-color: #191919;
                }
            }
        }
}
